body, html {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
    overflow: auto; /* Allows scrolling */
    scrollbar-width: none; /* For Firefox */
    background: linear-gradient(135deg, #ffffff 0%, #4E91E8 100%);
}
  
body::-webkit-scrollbar {
    display: none; /* For Chrome, Safari, and Edge */
}

.affine-cipher {
    height: 100vh;
    display: flex;
    flex-direction: column; /* Align elements vertically */
    justify-content: flex-start; /* Align grid below heading */
    align-items: center;
    gap: 5px;
    color: rgb(0, 0, 0);
    text-align: center;
    font-size: 2rem;
    direction: rtl;
    padding-top: 5px;
}

input {
    margin: 5px 0;
    padding: 5px;
    font-size: 1rem;
    width: 80%;
}

button {
    padding: 10px 15px;
    font-size: 2rem;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #4E91E8;
    color: white;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #8AA1BF;
}

h1 {
    margin: 10px 0; /* Reduce margin around the heading */
}

p {
    margin: 5px 0; /* Reduce margin around paragraphs */
}
