body, html {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
    overflow: auto; 
    scrollbar-width: none; 
    background: linear-gradient(135deg, #ffffff 0%, #4E91E8 100%);
  }
  
  body::-webkit-scrollbar {
    display: none; 
  }
  .cipher-selection {
    height: 100vh;
    display: flex;
    flex-direction: column; 
    justify-content: flex-start; 
    align-items: center;
    
    color: rgb(0, 0, 0);
    text-align: center;
    font-size: 1rem;
    direction: rtl;
    padding-top: 20px; 
  }
  
  .button-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px; 
    margin-top: 20px;
    
    padding: 20px; /* Padding to create space around buttons */
    border-radius: 8px;
    
  }
  
  .button-grid button {
    background: linear-gradient(to right, #4E91E8, #8AA1BF);
    color: #333;
    border: none;
    padding: 20px 10px; 
    font-size: 1rem ; 
    font-style: italic ;
    
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .button-grid button:hover {
    background: linear-gradient(to right, #ffd1dc, #ff6f91);
    
  }
  
  .button-grid button:active {
    background: linear-gradient(to right, #ff6f91, #ffd1dc); 
    transform: translateY(2px);  
  }

  .student-table {
    margin-top: 20px;
    width: 80%;
    margin: 10 auto;
    
    
}

table {
    width: 100%;
    border-collapse: collapse;
    overflow: hidden; /* To ensure borders and shadows are neatly contained */
}

th, td {
    padding: 12px 15px;
    text-align: center;
    border-bottom: 1px solid #ddd;
    font-size: 1.1rem;
}

th {
    background-color: #4E91E8; /* Header background color */
    color: white;
    text-transform: uppercase; /* Makes the header text uppercase */
    letter-spacing: 0.05em; /* Adds slight spacing between letters */
}

td {
    background-color: #f9f9f9; /* Light grey background for table cells */
    color: #333; /* Darker text color */
}

/* Alternating row colors for a cleaner look */
tr:nth-child(even) td {
    background-color: #f2f2f2;
}

/* Hover effect for table rows */
tr:hover td {
    background-color: #e0ebff; /* Light blue when hovering over rows */
    color: #333;
    cursor: pointer; /* Changes the cursor when hovering */
}

/* Add rounded corners to the entire table */
table {
    border-radius: 8px;
    overflow: hidden; /* To ensure the rounded corners apply neatly */
}

/* Optional: Add a little animation for smooth hover transitions */
td, th {
    transition: background-color 0.3s ease;
}




